const getData = async (url:string): Promise<string> => {
    let result:string = "";
    
    if(url.startsWith("www")){
        console.log("http");
        url = "https://"+url ;
    }
    console.log(url);
    try {
        const res = await fetch(url);
        console.log(res);
        const rjs = await res.json();
        result = JSON.stringify(rjs);
    }catch{
        alert("Json   !");
    }
    return result;
}

export default getData